	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Meetup: http://www.meetup.com/papers-we-love/events/220902753/\nPaper: http://www.cs.cmu.edu/~crary/819-f09/Strachey67.pdf\nSlides: http://bit.ly/1BnBb08\nAudio: http://bit.ly/1HAdXci\n\n---------------------------------------------------------------------------------------------\nSponsored by Two Sigma (@twosigma) and The Ladders (@TheLaddersDev)\n---------------------------------------------------------------------------------------------\n\nDescription\n---------------\nStrachey's lectures on \"Fundamental Concepts in Programming Languages\" provided an extremely broad survey of core issues in programming language design that provided much of the terminology we use today, including definitions of the kinds of polymorphism and the kinds of expressions we see in programming languages. Published as a paper many years later, Strachey's lectures provide an especially readable overview of programming languages concepts.\n\nBio\n----\nJohn Myles White (@johnmyleswhite) is a Julia hacker who loves thinking about the ways in which the design of technical programming languages influences the way in which we think about statistics and machine learning. He's written two books about machine learning for O'Reilly and works at Facebook.","meta":{"duration":"PT1H16M39S"},"presenter":{"name":null},"source":{"id":"cO41uoi5cZs","provider":"youtube"},"slug":"john-myles-white-on-fundamental-concepts-in-programming-languages","tag":["programming languages","paperswelove","Papers We Love","computer science","john myles white","julia","semantics","foundations of computing"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/cO41uoi5cZs/default.jpg","height":90,"width":120}},"title":"John Myles White on Fundamental Concepts in Programming Languages"}}]}},"pathContext":{"slug":"john-myles-white","tag":"john myles white"}}

